import React from "react";
import { GoogleLogin } from 'react-google-login';
import '../App.css';
import { useNavigate } from 'react-router-dom';

const clientId = '573641497027-te6g7e93nq3aakkcfepibr6f3rhqffcn.apps.googleusercontent.com';

function Login(props) {
    const navigate = useNavigate();
    const onSuccess = (res) => {
        console.log('[Login Success] curentUser:' , res.profileObj );
        sessionStorage.setItem('userProfile', JSON.stringify(res.profileObj));
        props.onLogIn();
        navigate('/posts');
    };

    const onFailure = (res) => {
        console.log('[Login failed] res:' , res);
        alert('Login failed');
    };

return (
    <div className='login'>
        <h1>To continue, please sign in to your Google account.</h1>
        <GoogleLogin 
          clientId={clientId}
          scope='https://www.googleapis.com/auth/userinfo.email'
        onSuccess={onSuccess}
        onFailure={onFailure}
        //cookiePolicy={'single_host_origin'}
        buttonText='Login'
        //isSignedIn={true}
        theme='dark'
        ></GoogleLogin>
     </div>                
)
}

export { Login }