import React, { useState, useEffect } from 'react';
import '../App.css';


const getComListHtml = async (postId) => {
    const response = await fetch(`https://dummyjson.com/posts/${postId}/comments`);
    const jsonResponse = await response.json();
    console.log(jsonResponse);

    const arrayOfLists = jsonResponse.comments.map(
        record => {   
            return (
            <li key={record.id}>{record.body}</li> 
            ) 
        }
      )
      return arrayOfLists;
};



function Comments(props) {
    const [apiResponse, setApiResponse] = useState("*** now loading ***");

    useEffect(() => {
        getComListHtml(props.postId).then(
            result => setApiResponse(result));
    }, []);

    return (
        <div className='comments'>
            <h1>Comments</h1>
            <ul>{apiResponse}</ul>
        </div>
    );
}

export { Comments };