import React, { useState } from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';
import { Login } from './components/Login';
import { Logout } from './components/Logout';
import { gapi } from 'gapi-script';
import { useEffect } from 'react';
import { PostList } from './components/PostList';
import { Post } from './components/Post';

const clientId = '573641497027-te6g7e93nq3aakkcfepibr6f3rhqffcn.apps.googleusercontent.com';

function App() {
  useEffect(() => {
    const initClient = () => {
      gapi.client.init({
        clientId: clientId,
        scope: 'https://www.googleapis.com/auth/userinfo.email'
      });
    };
    gapi.load('client:auth', initClient);
  });

  const [isLoggedIn, setIsloggedIn] = useState(() => sessionStorage.getItem('userProfile') !== null);
  const login = () => setIsloggedIn(true);
  const logout = () => setIsloggedIn(false);

  return (
    
    <div className="App">
      <Router>
        <Routes>
          <Route exact path='/login' element={<Login onLogIn={login}/>} />
          <Route path='/' element={isLoggedIn ? <PostList onLogOut={logout}/> : <Navigate to='login'/>} />
          <Route path='/posts' element={isLoggedIn ? <PostList onLogOut={logout}/> : <Navigate to='login'/>} />
          <Route path='/posts/:postId' element={isLoggedIn ? <Post onLogOut={logout}/> : <Navigate to='login'/>}/>
        </Routes>
      </Router>
    </div>
  )
};

export default App 


